.rotate {
    position: absolute;
    width: 20px;
    height: 20px;
    background-color: #cecece;
    border-radius: 50%;
    bottom: -50px;
    left: calc(50% - 10px);
    cursor: pointer;
    font-size: 12px;
    font-family: 'open-sans';
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
}



