.ZoomController {
    background-color: #333;
    border-radius: 2.5%;
    color: white;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: space-evenly;
        justify-content: space-evenly;
    -ms-flex-align: center;
        align-items: center;
    width: 125px;
    height: 40px;
    position: absolute;
    bottom: 15px;
    right: 25px;
    z-index: 100;
}

.ZoomController button {
    border: none;
    background-color: #333;
    border-radius: 50%;
    color: white;
    width: 25px;
    height: 25px;
    outline: none;
}

.ZoomController button:hover {
    background-color: #666;
}



